import React, { useEffect, useState } from "react";
import "./view.css";
import getBookingsInformation from "../api/bookings";

export default function OrderManger() {
  const [loading, setStateLoading] = useState(true);
  const [error, setErrorLoading] = useState(null);
  const [bookings, setBookings] = useState({
    bookings: [],
  });

  const getBookings = async () => {
    try {
      setStateLoading(true);
      const bookings = await getBookingsInformation();
      setBookings(bookings as any);
    } catch (e) {
      setErrorLoading(e);
    } finally {
      setStateLoading(false);
    }
  };

  const Booking = (index: number, order: any) => {
    if (index % 2) {
      return (
        <div className={"ordersOdd"}>
          <p>Order ID: {`${order.bookingId}`}</p>
          <p>Name: {`${order.name}`}</p>
          <p>State: {`${order.state}`}</p>
          <button>In Progress</button>
          <button>Complete</button>
        </div>
      );
    } else {
      return (
        <div className={"ordersEven"}>
          <p>Order ID: {`${order.bookingId}`}</p>
          <p>Name: {`${order.name}`}</p>
          <p>State: {`${order.state}`}</p>
          <button>In Progress</button>
          <button>Complete</button>
        </div>
      );
    }
  };

  useEffect(() => {
    getBookings();
  }, []);

  if (error)
    return (
      <div className={"view"}>
        <p>Failed to load resource for bookings</p>;{" "}
      </div>
    );

  return loading ? (
    <div className={"view"}>
      <p>loading...</p>
    </div>
  ) : (
    <div className={"view"}>
      <h3 className={"bannerLogo"}> Chloe Chart - Tarot and Oracle Readings</h3>
      {bookings.bookings.map((e, i) => {
        return Booking(i, e);
      })}
    </div>
  );
}
